html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", sans-serif;
  color: #1a171b;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease, background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}
.btn--reset {
  border: 0;
  background-color: rgba(255, 255, 255, 0);
  padding: 0;
  margin: 0;
}
.btn--centered {
  margin: 0 auto;
  display: flex;
  width: fit-content;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  color: #1a171b;
  transition: color 0.3s ease;
  outline: none;
  position: relative;
  padding: 5px;
  margin: -5px;
}
.link::after {
  position: absolute;
  left: 0;
  right: 5px;
  bottom: 8px;
  content: "";
  width: 100%;
  height: 20px;
  transform: scaleX(0);
  transform-origin: center;
  background-color: #000000;
  transition: transform 0.3s ease;
  z-index: -1;
}
@media (hover: hover) {
  .link:hover:not(.focus-visible) {
    text-decoration: none;
    color: #ffffff;
  }
  .link:hover:not(.focus-visible)::after {
    transform: scaleX(1);
  }
}
.link.focus-visible:focus {
  text-decoration: none;
  color: #ffffff;
}
.link.focus-visible:focus::after {
  transform: scaleX(1);
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 500px;
  padding: 20px 0;
  /*&.is-valid {
    input {
      border: 2px solid $color-malachite;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-malachite, 0.5);
      }
    }
  }*/
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  min-height: 40px;
  margin-left: 30px;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: -5px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-input.is-invalid input,
.custom-textarea.is-invalid input {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-invalid .custom-input__error,
.custom-textarea.is-invalid .custom-input__error {
  opacity: 1;
}

.custom-toggle {
  position: relative;
  display: flex;
  width: 500px;
  padding: 20px 0;
  /*&.is-valid {
    label span {
      color: $color-eucalyptus;
    }
  }*/
}
.custom-toggle__error {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 26px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 2px;
  left: 0;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  background-color: rgba(255, 255, 255, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 2px;
  left: 0;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1px solid rgba(0, 0, 0, 0.8);
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: rgba(0, 0, 0, 0.8);
}
.custom-toggle.is-invalid .custom-toggle__icon {
  border: 1px solid #ff1553;
}
.custom-toggle.is-invalid .custom-toggle__error {
  opacity: 1;
}

.custom-select {
  position: relative;
  width: 250px;
  padding: 30px 0;
  /* &.is-valid {
    .custom-select__button {
      border: 2px solid $color-eucalyptus;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-eucalyptus, 0.5);
      }
    }
  } */
}
.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;
}
.custom-select__button:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-select__button:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-select__list {
  position: absolute;
  bottom: 32px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
  border: 2px solid #000000;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  background-color: rgba(255, 255, 255, 0.2);
}
@media (hover: hover) {
  .custom-select__item[aria-selected=true]:hover:not(.focus-visible) {
    background-color: rgba(255, 255, 255, 0.2);
  }
}
.custom-select__item[aria-selected=true].focus-visible:focus {
  background-color: rgba(255, 255, 255, 0.2);
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: rgba(255, 255, 255, 0.4);
  }
}
.custom-select__item.focus-visible:focus {
  background-color: rgba(255, 255, 255, 0.4);
}
.custom-select__error {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.custom-select__text {
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #000000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 30px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__placeholder {
  opacity: 0;
}
.custom-select.is-invalid .custom-select__error {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  border: 2px solid #ff1553;
}
.custom-select.is-invalid .custom-select__button:focus, .custom-select.is-invalid .custom-select__button:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-select--placeholder .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}

.header {
  width: 100%;
  min-height: 80px;
  padding: 20px 0;
  background-color: #ffffff;
  position: fixed;
  z-index: 10;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}
@media (max-width: 1023px) {
  .header {
    min-height: 70px;
    padding: 15px 0;
  }
}
.header .container {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}
@media (max-width: 1023px) {
  .header .container {
    justify-content: space-between;
  }
}
.header__logo {
  flex-shrink: 0;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .header__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;
  }
}
.header__links {
  flex-basis: 167px;
  margin-left: 9px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .header__links {
    flex: auto;
    margin: 0;
    justify-content: flex-start;
  }
}
.header__link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  margin: -10px;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    outline: none;
  }
  .header__link:hover:not(.focus-visible) .header__link-icon--vk {
    stroke: #2787f5;
  }
}
.header__link.focus-visible:focus {
  outline: none;
}
.header__link.focus-visible:focus .header__link-icon--vk {
  stroke: #2787f5;
}
.header__link--call {
  font-size: 14px;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .header__link--call {
    margin-right: 40px;
    font-weight: 500;
    font-size: 23px;
    line-height: 24px;
  }
}
@media (max-width: 374px) {
  .header__link--call {
    margin-right: auto;
  }
}
.header__link--call::after {
  bottom: 10px;
}
.header__link--cart {
  height: 36px;
  position: relative;
  transition: transform 0.3s ease;
  transform: scale(1);
}
@media (hover: hover) {
  .header__link--cart:hover:not(.focus-visible) {
    transform: scale(1.2);
  }
}
.header__link--cart.focus-visible:focus {
  transform: scale(1.2);
}
.header__link--cart .cart-counter {
  width: 14px;
  height: 14px;
  top: 0;
  right: 0;
  position: absolute;
  font-weight: 700;
  font-size: 8px;
  line-height: 15px;
  color: #1a171b;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 5px;
  border: 1px solid #000000;
}
@media (max-width: 1023px) {
  .header__link--vk svg {
    width: 34px;
    height: 20px;
  }
}
.header__link-icon {
  pointer-events: none;
  transition: color 0.3s ease, stroke 0.3s ease;
}
.header__link-icon--vk {
  stroke: #1a171b;
}

.main-nav {
  flex-grow: 1;
  margin-right: auto;
  margin-left: 7px;
}
@media (max-width: 1023px) {
  .main-nav {
    margin: 0;
    padding: 20px;
    position: fixed;
    top: 70px;
    width: 100vw;
    height: calc(100% - 70px);
    background-color: #ffffff;
    z-index: 5;
    left: -100%;
    transition: transform 0.3s ease;
    overflow: hidden;
    overflow-y: auto;
  }
  .main-nav::before {
    content: "";
    width: 100%;
    height: 10px;
    background-color: #ffffff;
    position: absolute;
    top: -10px;
    left: 0;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  }
  .main-nav.is-active {
    transform: translateX(100%);
  }
}
.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  width: 100%;
  height: auto;
}
@media (max-width: 1023px) {
  .main-nav__wrapper {
    flex-direction: column;
    justify-content: flex-start;
  }
}
.main-nav__list {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    margin-bottom: 4rem;
  }
}
.main-nav__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .main-nav__item {
    width: 50%;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    width: 100%;
  }
}
.main-nav__item:not(:last-of-type) {
  margin: 0 20px 0 0;
}
@media (max-width: 1023px) {
  .main-nav__item:not(:last-of-type) {
    margin: 0 0 30px;
  }
}
.main-nav__link {
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-weight: 700;
    font-size: 26px;
    line-height: 25px;
    text-transform: uppercase;
    padding-right: 4rem;
  }
}
.main-nav__link::after {
  bottom: 5px;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__submenu {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: -100%;
  background-color: #ffffff;
  padding: 0 20px 20px;
  z-index: 6;
  display: none;
  transform: translateX(0);
  transition: transform 0.3s ease;
}
@media (max-width: 1023px) {
  .main-nav__submenu {
    display: block;
  }
  .main-nav__submenu::before {
    content: "";
    width: 100%;
    height: 10px;
    background-color: #ffffff;
    position: absolute;
    top: -10px;
    left: 0;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  }
  .main-nav__submenu.is-active {
    transform: translateX(-100%);
  }
}
.main-nav__submenu-toggle {
  width: 100%;
  height: 100%;
  display: none;
  justify-content: flex-end;
}
@media (max-width: 1023px) {
  .main-nav__submenu-toggle {
    display: flex;
  }
}
.main-nav__submenu-toggle svg {
  transform: rotate(180deg);
}
.main-nav__submenu-back {
  width: 100%;
  padding: 20px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 133%;
  text-transform: uppercase;
  color: #1a171b;
  position: relative;
}
.main-nav__submenu-back svg {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.main-nav__submenu-back span {
  pointer-events: none;
}
.main-nav__submenu-list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.main-nav__submenu-item:not(:last-of-type) {
  margin: 0 0 30px;
}
.main-nav__sublink {
  font-weight: 700;
  font-size: 22px;
  line-height: 20px;
  text-transform: uppercase;
  color: #1a171b;
}
.main-nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__toggle {
    display: flex;
    margin-left: -12px;
  }
}
.main-nav__toggle svg {
  pointer-events: none;
}

.line {
  fill: none;
  stroke: #000000;
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.line2 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.is-active .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -135;
  stroke-width: 6;
}

.is-active .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}

.is-active .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -135;
  stroke-width: 6;
}

.footer {
  padding: 40px 0;
  background-color: #2c39f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

